.navbar{
	background-color: #bebebe;
	color: black;
	display: flex;
	flex-direction: row;
	max-width: 100%;
}
.navbloc{
	padding: 10px 10px 10px 10px;
	border: solid black 2px;
	display: block;
	max-width: 30%;

	text-decoration: none;
	font-weight: bold;
	color: black;
}

.navbloc:hover{
	background-color: rgb(255, 169, 255);
}

.active{
	background-color: rgb(214, 4, 214);
}